










import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class UserAvatar extends Vue {
    /**
     * Name of the user.
     */
    @Prop()
    username: string;

    /**
     * Url of the avatar of the user.
     * Can be empty for placeholder.
     */
    @Prop()
    avatarUrl: string;
}
